<template lang="pug">
.curso-main-container.pb-3
  BannerInterno
  .container.tarjeta.tarjeta--blanca.p-4.p-md-5.mb-5
    #t_3_6
    .titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 3.6.	Instrumentos y técnicas de recolección de información

    .row.justify-content-center.align-items-center.mb-4(data-aos="flip-up")
      p La recolección de información durante y después de un recorrido turístico es una práctica clave para evaluar la calidad del servicio, identificar oportunidades de mejora y garantizar la sostenibilidad de la experiencia. Este proceso permite comprender tanto la percepción del visitante como el impacto generado en las comunidades anfitrionas y en el entorno natural. Según la Organización Mundial del Turismo (OMT, 2019), la evaluación de la experiencia turística debe basarse en métodos integrales que combinen la satisfacción del visitante con indicadores de sostenibilidad social, ambiental y económica. En el turismo étnico y comunitario, esto implica incorporar la voz de todos los actores —
      p visitantes, guías, familias anfitrionas y autoridades locales—, reconociendo que cada perspectiva contribuye al fortalecimiento del servicio turístico.
      p Los instrumentos más utilizados para la recolección de información son:

    .row.justify-content-center(data-aos="fade-right")
      .col-md-11.mb-5.mb-md-0
        LineaTiempoD
          .row(numero="Encuestas de satisfacción&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;" titulo="")
            .col-md-3
              figure
                img(src='@/assets/curso/temas/tema3/tema6/rec28-img1.svg', alt='Texto que describa la imagen')
                
            .col-md-9.mb-4.mb-md-0
              br
              p Son uno de los medios más directos para recopilar opiniones del visitante. Pueden aplicarse en formato físico o digital (formularios, códigos QR) e incluir preguntas cerradas (valoraciones numéricas) y abiertas (comentarios libres) que aborden aspectos como atención del guía, cumplimiento del itinerario, seguridad y autenticidad cultural.

          .row(numero="Entrevistas y grupos focales&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp" titulo="")
            .col-md-3
              figure
                img(src='@/assets/curso/temas/tema3/tema6/rec28-img2.svg', alt='Texto que describa la imagen')
                
            .col-md-9.mb-4.mb-md-0
              .p Permiten profundizar en percepciones y experiencias, especialmente en contextos donde la oralidad es la forma principal de comunicación. Estas técnicas son útiles para captar emociones, sugerencias o críticas constructivas.
              p Ejemplo: En comunidades indígenas o afrodescendientes, los grupos focales pueden realizarse con participación de líderes y visitantes, fomentando el diálogo horizontal y el respeto mutuo.

          .row(numero="Observación participante&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp" titulo="")
            .col-md-3
              figure
                img(src='@/assets/curso/temas/tema3/tema6/rec28-img3.svg', alt='Texto que describa la imagen')
            
            .col-md-9.mb-4.mb-md-0
              br
              p El guía o evaluador se integra al grupo y registra sistemáticamente las interacciones, comportamientos y niveles de satisfacción, sin intervenir directamente. Esta técnica es ideal para detectar aspectos no verbales del disfrute o incomodidad de los turistas.

          .row(numero="Técnicas participativas comunitarias" titulo="")
            .col-md-3
              figure
                img(src='@/assets/curso/temas/tema3/tema6/rec28-img4.svg', alt='Texto que describa la imagen')
            
            .col-md-9.mb-4.mb-md-0
              p En el turismo sostenible, la comunidad también evalúa la experiencia. Se pueden aplicar metodologías como la cartografía social, los murales de opinión o las asambleas de retroalimentación, donde los miembros expresan cómo percibieron el comportamiento de los visitantes y el impacto del recorrido en su territorio.

    br
    .row.justify-content-center.align-items-center.mb-4(data-aos="flip-up")
      p Los instrumentos de recolección de información deben ser co-diseñados junto con la comunidad, mediante procesos participativos que reconozcan y respeten sus sistemas de conocimiento y sus formas propias de evaluar la experiencia. Las preguntas no deben centrarse únicamente en la satisfacción del turista, sino también en el cumplimiento de los acuerdos comunitarios, como el respeto a los sitios sagrados, la compra justa de artesanías y el comportamiento responsable.
      p Además, pueden incorporarse métodos visuales y narrativos, como dibujos, mapas simbólicos o relatos personales, que resultan más inclusivos y expresivos que los cuestionarios escritos tradicionales.
      p Estas estrategias enriquecen la comprensión de la experiencia turística y fortalecen la participación activa y la apropiación cultural de todos los actores involucrados. A continuación se presenta de manera gráfica la utilidad específica de cada uno de los instrumentos de recolección de información.

